.app__footer-headtext{
    color: var(--color-white);
    font-family: var(--font-base);
    font-size: 32px;
    font-weight: 400;
    letter-spacing: .04em;
    line-height: 41.6px;
    margin-bottom: 1rem;
    text-transform: capitalize;
}
.app__logo{
    width: 210px;
    margin-bottom: .75rem;
}
.app__footer-container{
    display: flex;
    justify-content: space-evenly;
    text-align: center;
    padding: 0 2rem;
}
.app__footer-contact,
.app__footer-logo,
.app__footer-schedule{
    flex: 1 1;
    margin: 1rem;
}
.spoon-img{
    margin: 1rem 0;
    width: 45px;
}
.app__footer-icon{
    color: #fff;
    margin: .5rem;
    font-size: 23px;
}
.app__footer-copyright{
    text-align: center;
    margin-top: 3rem;
}
@media screen and (max-width:1150px) {
    .app__footer-container{
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
}