.app__newsletter-container{
    max-width: 900px;
    border: 1px solid var(--color-golden);
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    align-items: center;
    text-align: center;
    justify-content: center;
    background-color: var(--color-black);
    padding: 2rem 2rem;
}
.app__newsletter-input{
    display: flex;
    flex-direction: row;
    
    width: 100%;
    padding: 0 2rem;
}
.app__newsletter-input input{
    width: 620px;
    padding: 0 2rem;
    background-color: transparent;
    color: var(--color-grey);
    font-family: var(--font-base);
    font-size: 16px;
    text-transform: capitalize;
    border: none;
    border: 1px solid var(--color-golden);
    border-radius: 5px;
    margin-right: 2rem;
}
@media screen and (max-width:650px){
    .app__newsletter-input{
        padding: 0;
    }
}
@media screen and (max-width:550px){
    .app__newsletter-container{
        padding: 2rem 1rem;
    }
    .app__newsletter-input{
        display: flex;
        flex-direction: column;
        padding: 0;
    }
    .app__newsletter-input input{
        width: 100%;
        padding: .75rem 1rem;
        margin: 0;
        margin-bottom: 2rem;
    }
    .app__newsletter-input button{
        max-width: 112px;
        margin: 0 auto;
    }
}