.app__specialMenu{
    background: var(--color-black);
    flex-direction: column;
}
.app__specialMenu-title{
    margin-bottom: 2rem;
    text-align: center;
}
.app__specialMenu-menu{
    width: 100%;
    margin: 2rem 0;

    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: row;
}
.app__specialMenu-menu_wines,
.app__specialMenu-menu_cocktails{
    flex: 1;
    width: 100%;
    flex-direction: column;
}
.app__specialMenu-menu_img{
    width: 650px;
    margin: 0 2rem;
}
.app__specialMenu-menu_img img{
    height: auto;
    width: 100%;
}
.app__specialMenu-menu_items{
    display: flex;
    flex-direction: column;

    margin: 2rem 0;
    width: 100%;
}
.app__specialMenu-menu_heading{
    color: var(--color-white);
    font-family: var(--font-base);
    font-size: 45px;
    font-weight: 600;
    letter-spacing: .04em;
    line-height: 58.5px;
}

@media screen and (max-width:1150px){
    .app__specialMenu-menu{
        flex-direction: column;
        align-items: center;
        width: 100%;
    }
    .app__specialMenu-menu_img{
        width: 100%;
        margin: 3rem 0px;
    }
    .app__specialMenu-menu_img img{
        height: 920px;
    }
}
@media screen and (max-width:650px){
    .app__specialMenu-menu_img{
        width: 100%;
    }
    .app__specialMenu-menu_heading{
        font-size: 35px;
        line-height: 48.5px;
    }
}
