.app__gallery{
    background: var(--color-black);
    padding: 4rem 0 4rem 6rem;
    display: flex;
    flex-direction: row;
}
.app__gallery-content{
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    min-width: 500px;
    padding-right: 2rem;
}
.app__gallery-content_p{
    margin: 2rem 0 1rem 0;
    color: var(--color-grey);
    font-family: var(--font-alt);
    font-weight: 400;
    letter-spacing: 0.04em;
    text-transform: capitalize;
    line-height: 28px;
    font-size: 16px;   
}
.app__gallery-images{
    flex: 1;
    display: flex;
    flex-direction: row;
    max-width: 50%;
    position: relative;
}
.app__gallery-images_container{
    display: flex;
    flex-direction: row;
    overflow-x: scroll;
    width: max-content;
    -ms-overflow-style: none;
    scrollbar-width: none;
}
.app__gallery-images_container::-webkit-scrollbar{
    display: none;
}
.app__gallery-images_card{
    position: relative;
    min-width: 301px;
    height: 447px;
    margin-right: 2rem;
}
.app__gallery-images_card img{
    width: 100%;
    height: 100%;
    opacity: 1;
    object-fit: cover;
    transform: .5s ease;
}
.app__gallery-images_icon{
    position: absolute;
    font-size: 2rem;
    opacity: 0;
    color: #fff;
    transition: .5s ease;
    cursor: pointer;
}
.app__gallery-images_card:hover img{
    opacity: .35;
}
.app__gallery-images_card:hover .app__gallery-images_icon{
    opacity: 1;
}
.app__gallery-arrows{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    
    position: absolute;
    bottom: 5%;
    padding: 0 1rem;
    width: 100%;
}
.gallery__arrow{
    color: var(--color-golden);
    background: var(--color-black);
    font-size: 2rem;
    border-radius: 5px;
    cursor: pointer;
}
@media screen and (max-width:1150px){
    .app__gallery{
        flex-direction: column;
    }
    .app__gallery-images{
        max-width: 100%;
        margin: 5rem 0;
    } 
}
@media screen and (max-width:850px){
    .app__gallery{
        padding: 4rem 0 4rem 4rem;
    }
}
@media screen and (max-width:650px){
    .app__gallery{
        padding: 4rem 0 4rem 2rem;
    }
    .app__gallery-content{
       min-width: 100%;
    }
    .app__gallery-images_card{
        min-width: 240px;
        height: 320px;
    }
    .app__gallery-content_p{
        font-size: 14px;
    }
}
@media screen and (max-width:450px){
    .app__gallery-content_p{
        font-size: 12px;
    }
}