.app__menuItem {
    width: 100%;
    margin: 1rem 0;

    display: flex;
    flex-direction: column;
}

.app__menuItem-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
}



.app__menuItem-name {
    flex: 1;
}

.app__menuItem-dash {
    width: 90px;
    height: 1px;
    background: var(--color-golden);
    margin: 0 1rem;
}

.app__menuItem-price {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
}
.app__menuitem-tags {
    width: 100%;
    margin-top: 0.2rem;
}
.app__menuItem-tags p{
    font-family: var(--font-alt);
    color: var(--color-white);
    font-weight: 400;
    letter-spacing: 0.04em;
    text-transform: capitalize;
    line-height: 28px;
    font-size: 16px;
}