.app__chef{
    flex-direction: row;
}
.app__chef-quotient{
    display: flex;
    justify-content: flex-start;
    margin-bottom: 1rem;
}
.app__chef-quotient img{
    width: 47px;
    height: 40px;
    margin: 0 1rem 1rem 0;
}
.app__chef-content{
    display: flex;
    flex-direction: column;
    margin-top: 5rem;
    width: 100%;
}
.p__opensans{
    font-family: var(--font-alt);
}
.app__chef-sign{
    margin-top: 3rem;
    width: 100%;
}
.app__chef-sign img{
    width: 370px;
    margin-top: 3rem;
}
@media screen and (max-width:1150px){
    .app__chef{
        flex-direction: column;
    }
    
}
@media screen and (max-width:450px){
    .app__chef-sign img{
        width: 80%;
    }
    
}