

.app__award-history{
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin-top: 3rem;
}
.app__award-content{
    align-items: flex-start;
    display: flex;
    justify-content: flex-start;
    flex: 1 1;
    min-width: 230px;
    margin: 1rem;
}
.app__award-content img{
    height: 55px;
    width: 55px;
}
.app__award-content_name{
    display: flex;
    flex-direction: column;
    margin-left: 1rem;
}
@media screen and (max-width:1150px) {
    .app__award-img{
        margin-bottom: 0;
        margin-top: 5rem;
    }
}
@media screen and (max-width:650px){
    .app__award-img{
        margin-bottom: 0;
    }
}