.app__navbar{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--color-black);
    padding: 1rem 2rem;
    border: 2px solid black;

}
.app__navbar-logo{
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.app__navbar-logo img{
    width: 210px;
}
.app__navbar-links{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
}
.app__navbar-links li{
    margin: 0 1rem;
    font-family: var(--font-base);
    
}
.app__navbar-links li:hover{
    color: var(--color-grey);
}
.app__navbar-login{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font-family: var(--font-alt);
}
.app__navbar-login a{
    margin: 0 1rem;
    text-decoration: none;
    transition: .5s ease;
}
.app__navbar-login div{
    width: 1px;
    height: 30px;
    background-color: var(--color-grey);
}
.app__navbar-login a:hover{
    border-bottom: 1px solid var(--color-golden);
    cursor: pointer;
}
.app__navbar-smallscreen{
    display: none;
}
.app__navbar-smallscreen_overlays{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: var(--color-black);
    flex-direction: column;
    
   
}
 .app__navbar-smallscreen_toggleclose{
    font-size: 27px;
    color: var(--color-golden);
    cursor: pointer;
    position: absolute;
    top: 20px;
    right: 20px;
}
.app__navbar-smallscreen_toggleopen{
    color: #fff;
    font-size: 27px;
    cursor: pointer;
}
.app__navbar-smallscreen_links li{
    list-style: none;
    color: var(--color-golden);
    font-size: 2rem;
    font-family: var(--font-base);
    text-align: center;
    margin: 2rem;
}
.app__navbar-smallscreen_links li:hover{
    color: #fff;
}

@media screen and (max-width:1050px){
    .app__navbar-links{
        display: none;
    }
    .app__navbar-smallscreen{
        display: flex;
    }
}
@media screen and (max-width:650px) {
    .app__navbar{
        padding: 1rem;
    }
    .app__navbar-login{
        display: none;
    }
    .app__navbar-logo img{
        width: 110px;
    }
}